@font-face {
  font-family: tawbah100;
  src: url(./assets/fonts/tawbah100.ttf);
}
@font-face {
  font-family: Charter;
  src: url(./assets/fonts/Charter\ Regular.ttf)
}

@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rakkas&display=swap');

* {
  box-sizing: border-box;
}
p {
  color: #fff;
}
a {
  color: #fff;
  text-decoration: none;
  width: auto;
  height: auto;
}
h2 {
  color: #fff;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.scroll-top-btn {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1;
  width: 50px;
}

body {
  font-family: 'Raleway', sans-serif;
  background-color: #171717;
  margin: 0;
  padding: 0;
  padding-top: 60px;
}
header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  border-bottom: 0.2px solid #31313175;

  background: transparent;
  backdrop-filter: blur(2px);
}
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/* slider style */
.slider {
  margin: 0;
}
.container {
  margin: 0 auto;
 /* centers the sabiquns horizontally */
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 45vh;
  max-width: 1200px;
  width: 95%;
}
.text-wrapper {
  text-align: center;
  padding: 10px;
}
.ayah-text{
  text-align: center;
  margin: 0;
  font-family: tawbah100;
  font-size: x-large;
}
.translation-text {
  text-align: center;
  margin-top: 30px;
  font-size: medium;
  font-weight:bold;
}
/* Sabiquns */

.sabiqun-tab {
  background-color: #1b1b1b;
  padding-top: 25px;
}

.sabiquns {
  padding: 15px;
  max-width: 1200px;
  margin: 0 auto;
 /* centers the sabiquns horizontally */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 20px;
}

.sabiqun {
  margin-top: 30px;
  background-color: #31313175;
  border-radius: 5px;
  width: 100%;
  padding: 10px;
  border: 0.1px solid #2c2c2c;
  transition: background-color 0.1s ease-in-out;
}
.sabiqun:hover {
  background-color: #292929;
}
.sabiqun-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sabiqun-title {
  color: #e8e8fd;
  margin-bottom: 1.5em;
  font-size: 1rem;
  font-weight: 700;
  margin: 0;
  margin-bottom: 10px;
}
.sabiqun-image {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
}
.sabiqun-author {
  font-size: small;
  margin: 0;
  color: #9fa8bc;
}
/* sabiqun Detail Modal */
.sabiqun-detail-page {
  padding-right: 15px;
  padding-left: 15px;
}
article {
  margin: auto;
  max-width: 90%;
}

.sabiqun-detail-title {
  color: #e8e8fd;
  margin-bottom: 1.5em;
  font-size: 1.5rem;
  font-weight: 700;
}
.info_block {
  display: flex;
  gap: 6px;
  margin-bottom: 1em;
}
.info {
  font-size: 1rem;
  color: #98a0b3;
}
.last_update {
  display: inline-block;
  background-color: #5686f510;
  color: #5686f5;
  font-size: 1rem;
  padding: 0.5em 1em;
  border-radius: 0.5em;
}
.sabiqun-p {
  font-size: clamp(1rem, 0.875rem + 0.4vw, 1.125rem);
  color: #b8c0d4;
  line-height: 32px;
  letter-spacing: 0.50px;
  font-family: Charter;
}
@media screen and (max-width: 760px) {
  .resulullah {
    width: 100%;
  }
  .info_block {
      flex-direction: column;
 }
}

@media (min-width: 768px) {
  .sabiqun {
      width: calc(50% - 10px);
 }
}
@media (max-width: 768px) {
  .ayah-text{
      font-size: large;
 }
  .translation-text {
      font-size: small;
 }
}
